<template>
  <div class="bigdiv">
    <h3></h3><span>{{getCurrentTime}}</span>
    <el-card>
      <el-container>
        <el-header>
          <div></div>
          <div class="title">
            <span>{{ this.currenQuestion + 1 }}{{ an.title }}</span>
          </div>
        </el-header>
        <el-main class="main">
          <div class="op">
            <el-input v-for="(item,index) in this.an.answer.length" :key="index" placeholder="" v-model="checkList[index]" clearable></el-input>
          </div>
        </el-main>
        <el-footer>
          <div class="footerDiv">
            <div class="t">{{ this.currenQuestion + 1 }}/{{ this.totle }}</div>
            <div class="ic">
              <i class="el-icon-check">: {{ this.radiograde }}</i>
            </div>
            <div class="next">
              <el-button type="primary" @click="truebutton"></el-button>
            </div>
             <div class="commit">
              <el-button type="success" @click="commitBut"></el-button>
            </div>
            <div class="answer">
              <el-button round @click="getCurrentAnswer"></el-button>
            </div>
            <div class="answer">
              <router-link to="/index"><el-button round ></el-button></router-link>
            </div>
          </div>
        </el-footer>
      </el-container>
    </el-card>

    <!--  -->

    <el-dialog title="" :visible.sync="dialogVisible" width="80%" @closed="closeback">
      <span>{{ this.radiograde * 2 }}</span>
      <span slot="footer" class="dialog-footer">
        <router-link to="/index"><el-button round ></el-button></router-link>
        <el-button @click="AllRegame"></el-button>
        <el-button type="primary" @click="checkFalsetitel"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="answerDialogVisible" width="30%">
      : <span v-for="(item,index) in this.currentAnswer" :key="index">
           {{item}}<span style="color:red"></span><br/>
          </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="answerDialogVisible = false"> </el-button>
        <el-button type="primary" @click="answerDialogVisible = false"> </el-button>
      </span>
    </el-dialog>



    <!--  -->

    <el-dialog title="" :visible.sync="falseDialogVisible" width="80%" @closed="closeback">
        <div v-for="(item,index) in this.falseTitel" :key="index">
          <strong>{{index+1}}{{item.title}} </strong><br/>
           : <span v-for="(items,index) in item.answer" :key="index">
              <span style="color:red">{{items}}</span><br/>
          </span>
          <br/>
          <br/>
        </div>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="reFalseTitle"> </el-button> -->
        <router-link to="/index"><el-button round ></el-button></router-link>
        <el-button type="primary" @click="reFalseTitle"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {listData} from '../datas/Data4'

export default {
  data() {
    return {
      //
      listData: [],
      //
      currentListData: [],
      //
      an: {
        title: "",
        answer: [],
      },
    
      //
      checkList:[],
      //
      currentAnswer: [],
      //
      falseTitel: [],
      //
      radiograde: 0,
      //
      currenQuestion: 0,
      //
      totle: 0,
      //
      dialogVisible: false,
      //
      answerDialogVisible: false,
      //
      falseDialogVisible: false,
      //
      count: 10,

      //
      time:Date.now()
    };
  },
  created() {
    this.currentListData = this.getRandomTitle(listData, this.count);
    this.getDatas();

     var that =this;
        setInterval(function(){
            that.time=new Date().getTime();
        },1000)
  },
  methods: {
    getDatas: function () {
      //
      var currentnum = this.currenQuestion;
      this.an.title = this.currentListData[currentnum].title;
      this.an.answer = this.currentListData[currentnum].answer;
      //
      this.totle = this.currentListData.length;
    },
    //
    truebutton: function () {
      var userAnswer = this.checkList;      //
      var reAnswer = this.an.answer;        //
      console.log(reAnswer);
      let result =this.JudgeResult(userAnswer,reAnswer);
      if(this.checkList.length >0){
          if (result) {
            this.radiograde++;
          }else{
           this.falseTitel.push(this.currentListData[this.currenQuestion]);
          }
      }
      if (this.currenQuestion < this.totle - 1) {
        if (this.checkList.length > 0) {
          this.currenQuestion = this.currenQuestion + 1;
          this.getDatas();
          this.reGo();
        } else {
          alert("");
        }
      } else {
        alert("");

        //
        this.dialogVisible = true;
      }
    },
    //
    reGo: function () {
    //   this.answer = 5;
    this.currentAnswer=[];
      this.checkList=[];
    //   this.radio = 5;
    },
    //
    AllRegame: function () {
      this.currentListData = this.getRandomTitle(listData, this.count);
      this.reGo();
      this.currenQuestion = 0;
      this.totle = 0;
      this.radiograde = 0;
      this.falseTitel=[];
      this.getDatas();
      this.dialogVisible = false;
    },

    //n
    getRandomTitle: function (arr, count) {
      var newList = [];
      var List = arr.slice(0);
      var i = arr.length;
      var indexarr = [];
      for (var j = 0; j <= 100; j++) {
        var index = Math.floor(Math.random() * i); //0~9
        if (indexarr.indexOf(index) == -1) {
          newList.push(List[index]);
          indexarr.push(index);
        }
        if (newList.length == count) {
          break;
        }
      }
      return newList;
    },
    //
    getCurrentAnswer: function () {
        this.getAnswer();
        this.answerDialogVisible =true;

    },
    //
    commitBut:function(){
        alert("");

        this.dialogVisible = true;
    },

    //
    checkFalsetitel:function(){ 
      this.dialogVisible= false;
      this.falseDialogVisible=true;
    },

    //
    reFalseTitle: function(){
      this.falseDialogVisible = false;
      this.AllRegame();
    },

    //turefalse
    JudgeResult: function(arr1,arr2){
        /**
         * param : arr1: 
         * param : arr2: 
         * result : true,false
         */
        let temp=[];
        for(let i=0;i<=arr1.length-1;i++){
          temp[i]=arr1[i].toUpperCase();
        }


        for(let i=0;i<=arr2.length-1;i++){
            if(temp.indexOf(arr2[i])== -1){
                return  false;
            }
        }
        return true;
    },

    //
    getAnswer: function(){
        let tempArray =[];
        this.an.answer.forEach( i => {
           tempArray.push(i);
        });
        let result =this.JudgeResult(tempArray,this.currentAnswer);
        if(result){
            this.currentAnswer.push(tempArray);
        }
        return;
    },

    //
    closeback:function(){
      this.AllRegame();
    }

  },
  computed:{
     getCurrentTime:function(){
            return dateFormat("YYYY/mm/dd HH:MM:SS",new Date(this.time));
        },
  }
  
};

function dateFormat(fmt, date) {
    let ret;
    const opt = {
        "Y+": date.getFullYear().toString(),        // 
        "m+": (date.getMonth() + 1).toString(),     // 
        "d+": date.getDate().toString(),            // 
        "H+": date.getHours().toString(),           // 
        "M+": date.getMinutes().toString(),         // 
        "S+": date.getSeconds().toString()          // 
        // 
    };
    for (let k in opt) {
        ret = new RegExp("(" + k + ")").exec(fmt);
        if (ret) {
            fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
        };
    };
    return fmt;
}
</script>
<style scoped>
.a {
  position: relative;
  display: flex;
  align-content: space-around;
  flex-direction: column;
  box-sizing: border-box;
}

.bigdiv {
  width: 1200px;
  margin: auto;
  box-sizing: border-box;
}

.title,
.op {
  margin-top: 20px;
  height: 100%;
  box-sizing: border-box;
}
.radios {
  margin-top: 10px;
}

.next {
  margin-bottom: 10px;
}
.t {
  margin-left: 50px;
  margin-bottom: 10px;
}
.ic {
  width: 100px;
  margin-left: 100px;
  margin-bottom: 10px;
}

.el-card {
  height: 500px;
}

.main {
  height: 300px;
}
.footerDiv {
  display: inline-flex;
  align-items: center;
}

/*  */
.answer ,.commit{
  margin-left: 50px;
  margin-bottom: 10px;
}
</style>