<template>
  <div class="bigdiv">
    <h3></h3>
    <el-card>
      <div class="a">
        <div></div>
        <div class="title">
          <span>{{this.currenQuestion + 1 }}{{ an.title }}</span>
        </div>
        <div class="op">
          <el-radio-group v-model="radio" @change="select">
            <div v-for="(item, index) in an.option" :key="index" class="radios">
              <el-radio :label="index">{{ item }}</el-radio>
            </div>
          </el-radio-group>
        </div>
        <div class="t">{{ this.currenQuestion + 1 }}/{{ this.totle }}</div>
        <div class="ic">
          <i class="el-icon-check">: {{ this.radiograde }}</i>
        </div>
        <div class="next">
          <el-button type="primary" @click="truebutton"></el-button>
        </div>
      </div>
    </el-card>

    <!--  -->

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="80%"
    >
      <span>{{this.radiograde*1}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="AllRegame"></el-button>
        <el-button type="primary" @click="dialogVisible = false"
          > </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {listData} from '../datas/data';

export default {
  data() {
    return {
      //
      listData:[],
      //
      currentListData:[],
      //
      an: {
        title: "",
        option: [],
        answer: "",
      },
      //
      radio: 5,
      //
      answer: 5,
      //
      radiograde: 0,
      //
      currenQuestion: 0,
      //
      totle: 0,
      //
      dialogVisible: false,

      //
      count:30
    };
  },
  created() {
    this.currentListData=this.getRandomTitle(listData,this.count);
    this.getDatas();
  },
  methods: {
    getDatas: async function () {
      //
      var currentnum = this.currenQuestion;
      this.an.title = this.currentListData[currentnum].title;
      this.an.option = this.currentListData[currentnum].option;
      this.an.answer = this.currentListData[currentnum].answer;

      //
      this.totle = this.currentListData.length;
    },

    select: function (value) {
      this.answer = value;
    },

    //
    truebutton: function () {
      var userAnswer = this.answer;
      var reAnswer = this.an.answer;
      if (userAnswer == reAnswer) {
        this.radiograde++;
      }

      if (this.currenQuestion < this.totle - 1) {
        if (this.answer != 5) {
          this.currenQuestion = this.currenQuestion + 1;
          this.getDatas();
          this.reGo();
        } else {
          alert("");
        }
      } else {
        alert("");
        
        //
        this.dialogVisible=true;
      }
    },
    //
    reGo: function () {
      this.answer = 5;
      this.radio = 5;
    },
    //
    AllRegame:function(){
      this.currentListData=this.getRandomTitle(listData,this.count);
        this.reGo();
        this.currenQuestion=0;
        this.totle=0;
        this.radiograde=0;
        this.getDatas();
        this.dialogVisible = false;
        
    },

    //n
    getRandomTitle: function(arr,count){
      var  newList=[];
	  	var  List= arr.slice(0);
	  	var  i=arr.length;
		  var  indexarr=[];
		  for(var  j=0;j<=10000;j++){
		    	var  index =Math.floor(Math.random()*i);//0~9
			if (indexarr.indexOf(index) == -1) {
				newList.push(List[index]);
				indexarr.push(index);
			}
			if (newList.length==count) {
				break;
			};
		}
		return newList;

    }
  },
};


</script>
<style scoped>
.a {
  position: relative;
  display: flex;
  align-content: space-around;
  flex-direction: column;
  box-sizing: border-box;
}

.bigdiv {
  width: 1200px;
  margin: auto;
  box-sizing: border-box;
}

.title,
.op {
  margin-top: 20px;
  height: 100%;
  box-sizing: border-box;
}
.radios {
  margin-top: 10px;
}
.next {
  position: absolute;
  left: 560px;
  top: 400px;
  margin-bottom: 10px;
}
.t {
  position: absolute;
  left: 20px;
  top: 400px;
  margin-bottom: 10px;
}
.ic {
  width: 100px;
  position: absolute;
  left: 120px;
  top: 400px;
  margin-bottom: 10px;
}

.el-card {
  height: 500px;
}
</style>